<template>
  <el-row class="form-bg">
    <el-col :span="10" :offset="6">
      <el-form ref="form" :rules="rules" :model="forminfo" label-width="100px">
        <el-form-item label="角色名称" prop='rolename'>
           <el-input v-model="forminfo.rolename"></el-input>
        </el-form-item>
        <el-form-item label="权限菜单">
           <el-tree
              ref="tree"
              :data="menulist"
              show-checkbox
              :check-strictly="isStrictly"
              node-key="id"
              default-expand-all
              :props="{ children: 'children',label: 'title'}">
          </el-tree>
        </el-form-item>
        <el-form-item label="状态">
          <el-switch v-model="forminfo.status" :active-value="1" :inactive-value="2"></el-switch>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submit">立即{{$route.params.id ? '修改':'创建'}}</el-button>
          <el-button @click="reset">重置</el-button>
        </el-form-item>
      </el-form>
    </el-col>
  </el-row>
</template>

<script>
// tree树形控件
  // check-strictly 选项 是否严格模式
    //false 不是严格模式， 父子是关联的。 父点击了子全部选中，子点击了父也会被勾中   默认！
    //true   是严格模式， 父子不是关联的。 父点击了子全部选中，子点击了父也会被勾中

    // 修改页面，一进入就需要将模式设置为严格模式，且严格模式生效之后（DOM渲染完成），再去进行数据回显，数据回显之后设置为非严格模式

import {  addRole,editRole,getRoleList } from "@/request/role";
import { mapGetters,mapActions } from "vuex"
export default {
  data() {
    return {
      forminfo: {
        rolename:"",
        menus:[],
        status: 1, // 状态，1是正常，2是禁用
      },
      isStrictly:false,
      rules:{
          rolename: [{ required: true, message: '请输入角色名称', trigger: 'blur' }],
      }
    };
  },
  created() {
    // 如果是修改页面，就请求当前页面的数据！
    if (this.$route.params.id) {
      this.getinfo();
    }
  },
  computed: {
     ...mapGetters({
        menulist:"menu/menulist"
     })
  },
  mounted() {
    if(!this.menulist.length){
      this.getMenuList();
    }
  },
  methods: {
    ...mapActions({
      getMenuList:"menu/getlist"
    }),
    async getinfo() {
      this.forminfo = await getRoleList(this.$route.params.id);
      let menuKeys = this.forminfo.menus.split(","); 
      // 设置树形控件的数据回显！
      this.isStrictly = true; // 父子严格模式，父是父子是子，这样不出问题！
      this.$nextTick(()=>{  // 严格模式的属性控件渲染完成，再去数据回显！
          this.$refs.tree.setCheckedKeys(menuKeys);
          this.isStrictly = false;  // 设置为非严格模式，让父子再关联！
      })
    },
    submit() {
      // 验证
      if(!this.$refs.tree.getCheckedKeys().length){
        this.$message.warning('请选择菜单节点');
        return;
      }
      //  this.$refs.tree.getCheckedKeys().concat(this.$refs.tree.getHalfCheckedKeys());  子局部选中，父也被带入！
      this.forminfo.menus = this.$refs.tree.getCheckedKeys().concat(this.$refs.tree.getHalfCheckedKeys());
      // return;
      this.$refs.form.validate(valid=>{
        if(valid){
          // 提交数据
          if (this.$route.params.id) {
            editRole(this.$route.params.id, this.forminfo);
          } else {
            addRole(this.forminfo);
          }
        }else{
            this.$message.warning('验证失败')
        }
      })
    },
    reset(){
      if (this.$route.params.id) {
        this.getinfo(); 
      }else{
        this.$refs.form.resetFields();
      }
       
    }
  },
  components: {},
};

// BootStrap
</script>
<style scoped>
</style>


